import React from 'react'
import Link from 'gatsby-link'

import homeimage from '../../images/home-page-image.jpg'

const HemorrhoidBanding = () => (
  <div className="service-content">
    <img alt="Home Page Image" src={homeimage} className="img-responsive" />
    <h1 className="main-subject">Hemorrhoid Banding</h1>
    <h2 className="sub-topic">Hemorrhoids</h2>
    <p>
      Hemorrhoids are enlarged or swollen veins in the lower rectum. 
      The most common symptoms of hemorrhoids are rectal bleeding, 
      itching, and pain. You may be able to see or feel hemorrhoids 
      around the outside of the anus, or they may be hidden from 
      view, inside the rectum.
      <br />
      <br /> 
      Hemorrhoids are common, occurring in both men and women. 
      Although hemorrhoids do not usually cause serious health 
      problems, they can be annoying and uncomfortable. Fortunately, 
      treatments for hemorrhoids are available and can usually 
      minimize the bothersome symptoms.
    </p>
    <h2 className="sub-topic">Hemorrhoid Symptoms</h2>
    <p>
      Hemorrhoids are more common in older people and in those who 
      have diarrhea, pelvic tumors, during or after pregnancy, and 
      in people who sit for prolonged periods of time and/or strain 
      (push hard) to have a bowel movement.
    </p>
    <h3>Symptoms of hemorrhoids can include the following</h3>
    <ul>
      <li>Painless rectal bleeding</li>
      <li>Anal itching or pain</li>
      <li>Tissue bulging around the anus</li>
      <li>Leakage of feces or difficulty cleaning after a bowel movement</li>
    </ul>
    <p>
      <ins>Rectal bleeding</ins> -- Many people with hemorrhoids notice bright 
      red blood on the stool, in the toilet, or on the toilet tissue 
      after a bowel movement. While hemorrhoids are one of the most 
      common reasons for rectal bleeding, there are other more serious 
      causes. It is not possible to know what cause of rectal bleeding 
      unless you are examined by your healthcare provider.
      <br />
      <br />
      <ins>Itching</ins> -- Hemorrhoids cause itching and irritation of 
      skin around the anus. 
      <br />
      <br />
      <ins>Pain</ins> -- Hemorrhoids can be painful. If you have severe 
      pain, call your physician immediately.
    </p>
    <h2 className="sub-topic">Hemorrhoids Diagnosis</h2>
    <p>
      To diagnose hemorrhoids, your physician will examine your rectum 
      and anus, and may insert a gloved finger into the rectum or if 
      there is bleeding testing will be a procedure that allows your 
      physician to look inside the anus called colonoscopy.
    </p>
    <h2 className="sub-topic">Hemorrhoid Treatments</h2>
    <p>
      Constipation can lead to rectal bleeding and a tear in the anus, 
      called an anal fissure. Pushing and straining to move your bowels 
      can worsen existing hemorrhoids and increase the risk of developing 
      new hemorrhoids. As initial treatment of hemorrhoid; you must avoid 
      constipation. Below is several tips: 
      <br />
      <br />
      <ins>Fiber supplements</ins> -- Fiber is one of the best ways to 
      soften your stools. Fiber is found in fruits and vegetables. 
      Include about 20 grams of fiber to your daily diet. 
      <br />
      <br />
      <ins>Laxatives</ins> -- If increasing fiber in your daily diet 
      does not relieve your constipation, you can try a laxative. 
      Laxatives are not addictive and it may actually prevent 
      long-term problems with constipation. 
      <br />
      <br />
      <ins>Warm baths</ins> -- Fill up the bathtub with warm water and 
      soak your rectal for about 15 minutes several times a day. Do not 
      add soap or bubble bath in water. This process will improve blood 
      flow and relaxes the muscle around the anus. 
      <br />
      <br />
      <ins>Other treatments</ins> -- Various over the counter creams 
      and suppositories are available to relieve Pain, itching and 
      inflammation however you should not use these product long period 
      of time unless you consult your physician.
    </p>
    <h2 className="sub-topic">Hemorrhoid Banding</h2>
    <p>
      If your hemorrhoids still causes problem after using the above 
      exercises you may want to consider Hemorrhoid banding which is 
      the most widely used procedure. 
      <br />
      <br />
      Hemorrhoid banding is a painless same day procedure and 
      successful in most patients. Rubber bands or rings are placed 
      around the base of an internal hemorrhoid. As the blood supply 
      is restricted, the hemorrhoid shrinks and degenerates over 
      several days. Many patients report a sense of "tightness" after 
      the procedure. You may practice warm baths exercise for comfort. 
      Patients are encouraged to use fiber supplements to avoid 
      constipation. This procedure rarely causes serious complications.
      <br />
      <br />
      Please notify your physician if you experience severe or persistent 
      abdominal pain, chest pain, fever, difficulty swallowing or if you 
      have any questions.
    </p>
    <h2 className="sub-topic">Hemorrhoids Surgery</h2>
    <p>
      If you continue to have hemorrhoids despite all initial treatment 
      and hemorrhoid banding, you may require surgical removal of 
      hemorrhoids (hemorrhoidectomy). Surgery is the treatment of choice 
      for patients with large internal hemorrhoids. Hemorrhoidectomy 
      involves surgically removing excess hemorrhoidal tissue.
    </p>
  </div>
)

export default HemorrhoidBanding



